import { PageScaffold } from "@/components/page-scaffold";

export default function NewPatientPage() {
  return (
    <PageScaffold
      code="PRO-03"
      title="Novo Paciente + Convite"
      objective="Cadastro de paciente com envio de convite por e-mail ao final."
      primaryAction={{ label: "Criar e enviar convite", href: "/patients" }}
      sections={[
        "Nome, e-mail, telefone",
        "Objetivo inicial",
        "Peso/altura opcionais",
      ]}
    />
  );
}
